.root {
  width: 50px;
  height: 100vh;
  position: absolute;
  margin: 0;
  z-index: 3;
  min-height: 500px;
  background-color: #ff6666;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1em 0;
  box-sizing: border-box; }

.icon {
  color: #ffffff;
  margin: 0.5em 0; }

.icon:hover {
  color: #00ffef; }

.icon:active {
  color: #00ffef; }

.logo {
  flex: 1; }

.link {
  flex-direction: column;
  flex: 3;
  display: flex;
  justify-content: center; }

.social {
  flex-direction: column;
  flex: 1;
  display: flex;
  justify-content: flex-end; }

/* ------------------Mobile version ---------------------*/
@media all and (max-width: 425px) {
  .root {
    width: 100%;
    height: 50px;
    flex-direction: row;
    background-color: #02162b;
    min-height: 50px; }
  .logo {
    display: none; }
  .link {
    flex-direction: row;
    justify-content: space-around;
    font-size: 20px; }
  .social {
    display: none; } }



